import React, { Component } from "react";
import "./App.css";
import Fade from "react-reveal";
import Footer from "./Components/Footer";

class ModelB extends Component {
   render() {
      return (
         <div className="App">
            <div className="proj">
               <Fade bottom><h3>Making Another Keyboard to Satisfy My Addiction</h3></Fade>
               <Fade bottom duration={1200}><p>Over two years ago (<a href="https://brianchill.us/old/index.html">when this site looked like this</a>), I made a keyboard called the <a href="https://github.com/TrojanPinata/Model-K-Pico">Model K Pico</a>. It utilized a Raspberry Pi Pico, a somewhat new microcontroller at the time, to integrate a bunch of features I thought would be cool. It had an OLED display, volume knob, and all through hole components so that nearly anyone could build it. However, as it was my first keyboard, it did have a lot of flaws. Most importantly, the switchplate was made of acrylic and cracked during construction, the design was somewhat fragile feeling, and the board <i>felt</i> like I had made it from scratch. But it chugged on for years. I never had a single issue with it after it’s construction. I did make some changes to the firmware for fun, but overall, it was extremely solid. I am really proud of the Model K.</p>
               <img src="https://i.imgur.com/oxs17nZ.jpeg" alt="model k" />
               <p>But like everything, I now know a lot more about design and engineering. I am near the end of my education in computer engineering and I still love keyboards. And most importantly, I want to give the things I make to other people – even if they just put them on a shelf forever. So, I decided to make another keyboard. This one was called the <a href="https://github.com/TrojanPinata/Model-A">Model A</a>, and it failed within one iteration of my design process. I looked at the price to produce and pulled the plug immediately. That was a while ago, and I really never thought I was going to make another keyboard. The cost was way too high for a single keyboard. So, I shelved the project for a while. </p>
               <p>Or it would have been a while if I had not had people in my life who were also interested in keyboards. Talking with them made me want to design something that I could also give to them. And as such, in April 2024 I started making the <a href="https://github.com/TrojanPinata/Model-B">Model B</a>. As it is currently November 2024 when I am writing this, I think you can see that this took a while. I wanted to design all of the features of the previous board, streamline the layout a bit, and still keep that traditional <i>Brian</i> flair that comes with the things I make.</p>
               <img src="https://i.imgur.com/7yxFbYw.jpeg" alt="full shot" />
               <p>The board has the same OLED display, volume knob, and 75% layout, but also includes a special button for switching screens, a full acrylic case with Delrin switchplate, and integrated stand at ~7 degree angle. It also has a window specifically for looking at the microcontroller and components laid out on the board. I chose to use SMD components for a much more professional look, and I added hotswappable sockets to allow whoever gets this board to customize it to their liking. I felt like this would make the project something more of a collaboration and help people enjoy the thing I made them for longer. Oh, and as a cherry on top, it still uses the same Raspberry Pi Pico processor I used on the first one.</p>
               <img src="https://i.imgur.com/10xAQjR.jpeg" alt="close up" />
               <p>The keyboard is finished and gifted to those I have wanted to deliver them to, so I feel like I can write this now. But I am really proud of this project. It has a certain feeling to it that I cannot describe, and I really hope those who get it are as happy with it as I am. There are a couple of quirks, but I really only had one shot with designing and manufacturing this so I am happy.</p>
               <img src="https://i.imgur.com/dlf4HPK.jpeg" alt="a pretty nice photo" /> {/* Photography by https://www.kobebrian.com/ */}
               <p>I may do this again in the future, but if I do that then I am getting a bunch manufactured and I am selling some. The <a href="https://github.com/TrojanPinata/Model-B">Github for the project is here</a>. I may make a video about this – but probably not. </p>
               <p>Regardless and as always,</p>
               <p>Until next time.</p>
               </Fade>
            </div>
            {/*11/22/24*/}
            <Footer />
         </div>
      );
   }
}

export default ModelB;