import React, { Component } from "react";
import "./App.css";
import Fade from "react-reveal";
import Footer from "./Components/Footer";

class MOS extends Component{
   render() {
      return (
         <div className="App">
            <div className="proj">
               <Fade bottom><h3>mOS</h3></Fade>
               <Fade bottom duration={1200}>
                  <p><a href="https://github.com/TrojanPinata/mOS">mOS</a> is a very simple project: make a functioning operating system. I am not an operating systems programmer – I have not even taken an operating systems class yet - so the functionality is allowed to be limited. The goal is to get something that will at least boot.</p>
                  <p>There isn’t really that much for me to say about this project. I was inspired to do this by <a href="https://github.com/pac-ac/osakaOS">OsakaOS</a> and while I was already thinking about doing a OS project, I saw they had recommended a video series called <a href="https://www.youtube.com/channel/UCQdZltW7bh1ta-_nCH7LWYw">Write Your Own Operating System (WYOOS)</a>. So, during my fall break, I decided to marathon the videos and see what I could do. Being that you are reading this, I think you can see what happened. WYOOS is a fantastic resource and I recommend it to anyone who wants to get started with operating systems – especially from an object-oriented perspective. The only criticism I have is that Viktor doesn’t really explain some of the decisions he makes, which makes it a bit difficult to really understand the full scope of what is going on. That being said, this is still one of the best programming tutorial videos I have ever seen.</p>
                  <p>I posted my work in progress code to my <a href="https://github.com/TrojanPinata/mOS">GitHub</a> and I will try and finish it eventually. This is a pretty time consuming project so it probably won't be for a while. Also, yes – I know the current build does not work as I broke it right before posting :|</p>
                  <p>Anyways, until next time.</p>
                  <img src="https://i.imgur.com/Ui5L7Jh.png" alt="screenshot of OS post boot" />
               </Fade>
            </div>
            <Footer />
         </div>
      );
   }
}

export default MOS;